@import "~@/assets/scss/app.scss";
















































.bubble {
    position: absolute;
    // width: 100%;
}

.top {
    top: -40px;
    left: 0;
    @media screen and (min-device-width: 375px) {
        left: -5px;
        top: -10px;
    }
}

.bottom {
    right: 0;
    bottom: -40px;
    transform: rotate(180deg);
    @media screen and (min-device-width: 375px) {
        right: -5px;
        bottom: -10px;
    }
}
